var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('v-row',{attrs:{"align":"center","justify":"center"}},[_c('v-img',{attrs:{"src":require("@/assets/Outdoor Center Logo.png"),"max-width":"200"}})],1),_c('br'),_c('h1',[_vm._v("Press Release")]),_c('h4',{staticStyle:{"font-style":"italic"}},[_vm._v(" Blueberry Hill Outdoor Center, December 1, 2020 ")]),_c('p',[_c('b',[_vm._v("Blueberry Hill Outdoor Center (BHOC), long known as Blueberry Hill Ski Center, is now established as a 501(c)(3) non-profit company.")]),_vm._v(" BHOC ensures land preservation and recreational access within a unique and historic section of the Moosalamoo National Recreation Area in the Green Mountain National Forest. ")]),_c('p',[_vm._v(" In 1969, Tony Clark became involved with Blueberry Hill Inn and began to build a network of cross-country ski trails on Inn property and US Forest Service land. These trails became a vibrant focus of New England ski racing in the 1970s and 1980s, hosting races attended by local enthusiasts to collegiate skiers and Olympians. ")]),_c('p',[_vm._v(" 45 kilometers of trails, initially developed for winter use only, have evolved into a year-round trail network used by hikers, runners, mountain bikers, skiers, and snowshoers who visit from New England and beyond. For the last 50 years, Blueberry Hill Inn owners have been honored to share their property to provide information and access to the trails, and will continue to work with the USFS and BHOC as stewards. ")]),_c('p',[_vm._v(" The establishment of BHOC as a non-profit focuses on building a strong outdoor community that ensures that the land and trail system can be cherished and maintained for many years to come. ")]),_c('h3',[_vm._v("Description of Trails")]),_c('p',[_vm._v(" The Blueberry Hill trail system includes a long climb and descent over the shoulder of Romance Mountain - adjacent the Long Trail - and a spectacular view of the Green, Adirondack, and Taconic Mountains from the south side of Hogback Mountain (a site of many weddings, and even more blueberry picking). The trail system includes a segment of the Catamount Trail, the cross-country ski trail that extends the length of Vermont; connects with the trails of the Moosalamoo National Recreation Area; and directly to the Long Trail via the Sucker Brook Trail. ")]),_c('p',[_vm._v(" Though many cross-country ski centers and race venues have since transitioned to wider trails groomed for both skate and classic skiing, the joys of narrow, ungroomed winding trails recall a fundamental way of being in the woods in winter. ")]),_c('h3',[_vm._v("What to expect this winter")]),_c('p',[_vm._v(" This year, as we all work to find moments of peace from the strain of pandemic, access to nature and recreation is critical. This winter BHOC will offer cross-country ski, snowshoe, and microspike rentals; a sledding hill; socially-distanced and private indoor group space by reservation; and grab-and-go snacks and meals. BHOC has been the recipient of a small grant to help provide a safe experience during the pandemic, rigorously respecting state guidelines, while ensuring that visitors have a place to stay warm with family or friends as they venture outside. ")]),_c('p',[_vm._v(" Blueberry Hill Inn, like most of the hospitality industry, has seen an enormous decrease in business during Covid-19. For Inn owners Tony Clark and Shari Brown and their family, ensuring the future of the trails – both preservation and access to them – is paramount. The Inn team has used the time they would otherwise be spending making pancakes and cleaning rooms to invest in the Outdoor Center. Though the future of hospitality in Vermont is uncertain, the establishment of BHOC as a separate non-profit ensures that the forests, trails, and bridges that surround the Inn are here to stay. ")]),_c('p',[_vm._v(" Trails and trail access will be available all year round, supported through day-use donations, grants, and volunteers. ")]),_c('h3',[_vm._v("How YOU can help")]),_c('p',[_vm._v(" You can support BHOC by visiting and exploring the trails. Bring your family and friends and spread the word. All donations will be used to support trail maintenance and development, access and facilities to make the BHOC trails and programs more accessible to all and are tax deductible to the fullest extent of the law. ")]),_c('div',{attrs:{"align":"center"}},[_c('form',{attrs:{"action":"https://www.paypal.com/donate","method":"post","target":"_top"}},[_c('input',{attrs:{"type":"hidden","name":"hosted_button_id","value":"9RSPUY2VGNFUC"}}),_c('input',{attrs:{"type":"image","src":"https://www.paypalobjects.com/en_US/i/btn/btn_donateCC_LG.gif","border":"0","name":"submit","title":"PayPal - The safer, easier way to pay online!","alt":"Donate with PayPal button"}}),_c('img',{attrs:{"alt":"","border":"0","src":"https://www.paypal.com/en_US/i/scr/pixel.gif","width":"1","height":"1"}})])]),_c('br'),_c('br'),_c('br'),_c('br')],1)}
